.collection-div {
    margin-top: 30px;
}

.collection-items-div {
    margin: 30px auto 30px auto;
    width: 90%;
    box-shadow:0 0 8px #605856;
    font-family: 'Abril Fatface', cursive;
    padding-bottom: 10px;
}

.categories {
    list-style-type: none;
    display: -webkit-flex;
    display: flex;
    -webkit-justify-content: space-around;
            justify-content: space-around;
    margin: 0;
    padding: 0;
    line-height: 3.0;
    color: #66483F;
    background-color: white;
    font-family: 'Questrial', sans-serif;
}

.item-card-div {
    height: 100%;
    display: -webkit-flex;
    display: flex;
    padding: 0 5% 0 5%;
    -webkit-flex-flow: row wrap;
            flex-flow: row wrap;
    -webkit-align-content: flex-start;
            align-content: flex-start;
    overflow: auto;
}

.img-container {
    height: 50%;
}

.img-container > img {
    margin: 5%;
    max-width: 95%;
    height: 95%
  }

.card {
    border-radius: 2px;
    height: 450px;
    margin: auto;
    margin-bottom: 2%;
    position: relative;
    width: 250px;
    text-align: left;
    font-family: 'Questrial', sans-serif;
}

.card:hover {
    border-color: #FBAF37;
}

.card-title {
    color: #F75133;
    padding-right: 20px;
    font-size: 18px;
    line-height: 1
}

.card-text {
    color: #66483F;
    max-height: 50px;
    margin-bottom: 25px;
}

.btn {
    background-color: #50A3A4;
    border-color: #50A3A4;
    height: 40px;
    padding-top: 7px;
    border-radius: 50px;
    width: 100%
}

.btn:hover {
    color: #9BC1BC;
}
.post-item-div {
    margin-top: 30px;
}

.form-div {
    margin: 35px auto 30px auto;
    width: 55%;
    box-shadow:0 0 8px #605856;
    padding-bottom: 1px;
}

form {
    margin: 5% 0 5% 6%;
    color: #F75133;
    font-family: 'Questrial', sans-serif;
}

#post-submit-btn {
    margin-top: 2%;
    height: 45px;
    width: 100%;
    border-radius: 125px;
    color: white;
}

#map {
    height: 100%;
    width: 100%;
}

.form-head {
    height: 100%;
    padding: 10px 0 2px 15px;
}

.form-control {
    border: solid 1px #E7E6F7;
}
.requests-page-div {
    margin-top: 30px;
}

.requests-div {
    margin: 30px auto 30px auto;
    width: 66%;
    height: 100%;
    box-shadow: 0 0 8px #605856;
}

.request-card {
    border: 1px solid #CFD2CD;
    height: 75px;
    margin: 1% 1%;
    padding: 1% 1%;
}

.request-card:hover {
    border-color: #FBAF37;
}

.request-title {
    color: #F75133;
    font-size: 20px;
}

.request-description {
    color: #66483F;
}

.middle-col {
    line-height: 20px;
}

.middle-col-item {
    color: red;
    margin-right: 10px;
}

.request-item-img > img {
    height: 60%;
    width: 60%;
}

.request-btn {
    color: #f7f7f7;
    background-color: #50A3A4;
    border-color: #50A3A4;
    border-radius: 50px;
    height: 66%;
    width: 90%;
}

.request-link:hover {
    text-decoration: none;
    color: #9BC1BC;
}
@media all and (min-width: 480px) {
    .Login {
      padding: 60px 0;
    }
  
    .Login form {
      margin: 0 auto;
      max-width: 320px;
    }
  }
